import Vue from 'vue'
import VueRouter from 'vue-router'

import App from './App.vue'
import New from './components/New.vue'

Vue.use(VueRouter)

const Bar = { template: '<div>bar</div>' }
const Ranking = { template: '<div>ranking</div>' }

const routes = [
  {
    path: '/new',
    component: New
  },
  {
    path: '/continue',
    component: Bar
  },
  {
    path: '/ranking',
    component: Ranking
  }
]

const router = new VueRouter({
  routes
})

new Vue({
  el: '#app',
  router,
  render: h => h(App)
})



